.dev-modal {
  position: absolute;
  top: 50%;
  left: 50%;

  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;

  border-radius: 16px;
  box-shadow: 2px 2px 8px 1px rgba(0, 0, 0, 0.22);

  transform: translate(-50%, -50%) scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  .backdrop.is-hidden & {
    transform: translate(-50%, -50%) scale(1.2);
  }
}

.dev-modal__photo {
  margin-bottom: 10px;
  border-radius: 16px;
  max-width: 280px;

  @include mq(tablet) {
    min-width: 400px;
  }

  @include mq(desktop) {
    min-width: 450px;
  }
}

.dev-modal__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;

  // letter-spacing: 1px;
  transition: all 250ms;

  // text-shadow: 10px -10px 15px $accentColor, -10px 10px 15px $accentColor;
  // -webkit-text-stroke: 2px $accentColor;
  // -webkit-text-fill-color: transparent;
}

.dev-modal__title:hover,
.dev-modal__title:focus {
  letter-spacing: 8px;
  filter: drop-shadow(4px 8px 3px $accentColor);
}

.dev-modal__title-span {
  margin-top: 10px;
  @include font($fw: 900, $fs: 22px, $lh: 16px, $color: $accentColor);
}
